html {
  box-sizing: border-box;
}

*, ::after, ::before {
  box-sizing: inherit;
}

body {
  font-family: 'Lato', sans-serif;
  background-color: whitesmoke;
  max-width: 1000px;
  margin: auto;
  padding: 15px;
  line-height: 1.6;
  background-image: url("coins.jpg");
  background-repeat: no-repeat;
  background-position: center;
  resize: both;
  overflow: auto;
  background-size: auto 1300px;
}

